.Sidedrawer{
  position: fixed;
  top: 0;
  left: 0;
  padding-top: 5rem;
  background: var(--primary-color-light);
  height: 100vh;
  width: 45%;
  z-index: 2;
}

@media screen and (min-width: 990px){
  .Sidedrawer{
    display: none;
  }
}

.SocialMedia{
  width: 100%;
  justify-content: space-around;
  justify-self: center;
}

.SocialLink{
  color: var(--primary-color-dark-text);
}